export default {
	data() {
		return {
			likeList:[],
		}
	},
	onLoad() {
		this.getStorage()
	},
	methods: {
		getStorage(){
			this.likeList = uni.getStorageSync('like')
		},
		//动态详情
		detail(uid, openid) {
			uni.navigateTo({
				url: '/pages/roomDetail/roomDetail?uid=' + uid + '&openid=' + openid,
			})
		}
	}
}
